'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { PieChart, Pie, Cell, Legend, Tooltip, ResponsiveContainer } from 'recharts'

const categoryData = [
  { name: 'Food & Dining', value: 35, color: 'var(--color-chart-1)' },
  { name: 'Transport', value: 25, color: 'var(--color-chart-2)' },
  { name: 'Entertainment', value: 20, color: 'var(--color-chart-3)' },
  { name: 'Utilities', value: 12, color: 'var(--color-chart-4)' },
  { name: 'Other', value: 8, color: 'var(--color-chart-5)' },
]

export function CategoryBreakdown() {
  return (
    <Card className="bg-card">
      <CardHeader>
        <CardTitle>Category Distribution</CardTitle>
        <CardDescription>Spending breakdown by category</CardDescription>
      </CardHeader>
      <CardContent>
        <ResponsiveContainer width="100%" height={300}>
          <PieChart>
            <Pie
              data={categoryData}
              cx="50%"
              cy="50%"
              labelLine={false}
              label={({ name, value }) => `${name}: ${value}%`}
              outerRadius={80}
              fill="#8884d8"
              dataKey="value"
            >
              {categoryData.map((entry, index) => (
                <Cell key={`cell-${index}`} fill={entry.color} />
              ))}
            </Pie>
            <Tooltip formatter={(value) => `${value}%`} />
          </PieChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  )
}
