'use client'

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { ScrollArea } from '@/components/ui/scroll-area'
import { Badge } from '@/components/ui/badge'
import { ShoppingCart, Fuel, Zap, Coffee, Plane, ChevronRight } from 'lucide-react'
import {useEffect, useState} from "react";
import axioClient from "@/utils/axioClient";

// const transactions = [
//   { id: 1, name: 'Grocery Store', category: 'Food', amount: 65.50, icon: ShoppingCart, date: 'Today' },
//   { id: 2, name: 'Gas Station', category: 'Transport', amount: 45.00, icon: Fuel, date: 'Yesterday' },
//   { id: 3, name: 'Electric Bill', category: 'Utilities', amount: 120.00, icon: Zap, date: '2 days ago' },
//   { id: 4, name: 'Coffee Shop', category: 'Food', amount: 5.75, icon: Coffee, date: '3 days ago' },
