export function TransactionsList() {
    const [transactions, setTransactions] = useState([])

    useEffect(()=> {
        axioClient.get("/category/summary")
            .then((res)=> setTransactions(res.data) )
    }, [])
  return (
    <Card className="bg-card">
      <CardHeader>
        <CardTitle>Recent Transactions</CardTitle>
        <CardDescription>Your latest spending activities</CardDescription>
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-[320px] pr-4">
          <div className="space-y-3">
            {transactions.map((transaction) => {
              const Icon = transaction.icon
              return (
                <div key={transaction.id} className="flex items-center justify-between p-3 rounded-lg bg-background/50 hover:bg-background transition-colors">
                  <div className="flex items-center gap-3 flex-1">
                    <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center">
                      <Icon className="h-5 w-5 text-primary" />
                    </div>
                    <div className="flex-1">
                      <p className="text-sm font-medium text-foreground">{transaction.name}</p>
                      <p className="text-xs text-foreground/60">{transaction.date}</p>
                    </div>
                  </div>
                  <div className="flex items-center gap-3">
                    <div className="text-right">
                      <p className="text-sm font-semibold text-foreground">${transaction.amount}</p>
                      <Badge variant="secondary" className="text-xs mt-1">{transaction.category}</Badge>
                    </div>
                    <ChevronRight className="h-4 w-4 text-foreground/30" />
                  </div>
                </div>
              )
            })}
          </div>
        </ScrollArea>
      </CardContent>
    </Card>
  )
}
