'use client'

import { Card, CardContent } from '@/components/ui/card'
import { TrendingUp, Wallet, Tag, ArrowUpRight } from 'lucide-react'

interface OverviewProps {
  data: {
    totalSpent: number
    transactionCost: number
    categoriesCount: number
    transactionsCount: number
  }
}

export function SpendingOverview({ data }: OverviewProps) {
  const metrics = [
    {
      label: 'Total Spent',
      value: `$${(data.totalSpent / 1000).toFixed(1)}k`,
      icon: Wallet,
      color: 'from-primary/20 to-primary/5',
      trend: '+12.5%',
      trendUp: true,
    },
    {
      label: 'Transaction Costs',
      value: `$${data.transactionCost.toFixed(2)}`,
      icon: ArrowUpRight,
      color: 'from-chart-1/20 to-chart-1/5',
      trend: '+3.2%',
      trendUp: false,
    },
    {
      label: 'Categories',
      value: data.categoriesCount.toString(),
      icon: Tag,
      color: 'from-chart-2/20 to-chart-2/5',
      trend: 'Active',
      trendUp: true,
    },
    {
      label: 'Total Transactions',
      value: data.transactionsCount.toString(),
      icon: TrendingUp,
      color: 'from-chart-3/20 to-chart-3/5',
      trend: '+24 this month',
      trendUp: true,
    },
  ]

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
      {metrics.map((metric, index) => {
        const Icon = metric.icon
        return (
          <Card key={index} className="bg-card hover:bg-card/80 transition-colors">
            <CardContent className="pt-6">
              <div className="flex items-start justify-between">
                <div>
                  <p className="text-sm font-medium text-foreground/60 mb-2">{metric.label}</p>
                  <p className="text-3xl font-bold text-foreground">{metric.value}</p>
                  <p className={`text-xs mt-2 ${metric.trendUp ? 'text-chart-2' : 'text-destructive'}`}>
                    {metric.trend}
                  </p>
                </div>
                <div className={`p-3 rounded-lg bg-gradient-to-br ${metric.color}`}>
                  <Icon className="h-5 w-5 text-primary" />
                </div>
              </div>
            </CardContent>
          </Card>
        )
      })}
    </div>
  )
}
